@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@200&family=Roboto:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  transition: all 0.3s ease;
  font-family: 'Roboto' , 'Prompt' , sans-serif;
}

.container{
  width: 100vw;
  background-color: #F5F5F5;
}

.form{
  padding: 20px 200px;
  background-color: #F5CA2F;
}

.form input[type="text"]{
  font-size: 18px;
  width: calc(100% - 40px);
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  box-shadow: 8px 10px 13px -8px rgba(0,0,0,0.47);
  -webkit-box-shadow: 8px 10px 13px -8px rgba(0,0,0,0.47);
  -moz-box-shadow: 8px 10px 13px -8px rgba(0,0,0,0.47);
}

.form input[type="text"]:focus{
  outline: none;
}

.movie-container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
}

.zoom{
  width: 100%;
  height: 500px;
  overflow: hidden;
  margin: 0 auto;
  cursor: pointer;
}

.movie-grid-con{
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.movie-grid{
  width: 100%;
  height: 100%;
  background-color: #00000055;
}

.movie-grid-con:hover{
  transform: scale(1.1);
}

.movie-name{
  padding: 40px;
  font-size: 40px;
  color: #F5F5F5;
  font-weight: 800;
}

.movie-description{
  padding: 40px;
  font-size: 20px;
  color: #F5F5F5;
  font-weight: 800;
}

